.Tile {
    height: 100px;
    width: 100px;
    border: solid black;
    -webkit-box-shadow: 0 4px 8px 0 #aaa;
            box-shadow: 0 4px 8px 0 #aaa;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 3em;
}

.Tile p {
    margin: 0
}

@media (max-width: 450px) {
    .Tile {
        font-size: 2em;
        height: 60px;
        width: 60px;
    }
}

.Tile:hover {
    -webkit-box-shadow: 0 6px 12px 0 #aaa;
            box-shadow: 0 6px 12px 0 #aaa;
}

img {
    max-width: 100%;
}


